<template>
	<view>
		<view class="gui-margin-top gui-padding">
			<text class="gui-text gui-color-gray">拖动定位</text>
		</view>
		<view class="">
			<map style="width:750rpx; height:300px;" 
			:scale="16" @regionchange="regionchange" id="map"
			:latitude="latitudeDef" :longitude="longitudeDef" 
			:controls="controls"></map>
		</view>
		<view class="gui-margin-top gui-padding">
			<text class="gui-text-small gui-block-text gui-text-center">中心坐标 : {{center[0]}}, {{center[1]}}</text>
		</view>
	</view>
</template>
<script>
export default {
	data() {
		return {
			latitude  : 39.909, // 纬度
			longitude : 116.39742 ,//经度,
			// 默认值与拖拽值分开解决 安卓赋值bug
			latitudeDef  : 39.909,    // 纬度默认值
			longitudeDef : 116.39742, // 纬度默认值
			controls  : [{
				id : 1000,
				position : {left:uni.upx2px(350), top:150, width:30, height:30},
				iconPath  : '../../../static/location.png',
				
			}],
			locationName  : '',
			center : ['', '']
		}
	},
	onLoad:function(){
		uni.showLoading();
		// 默认情况下获取当前位置
		// 请仔细阅读 注意事项!!
		// https://uniapp.dcloud.io/api/location/location
		uni.getLocation({
			type: 'gcj02',
			success:(res)=>{
				uni.hideLoading();
				console.log(res);
				console.log('当前位置的经度：' + res.longitude);
				console.log('当前位置的纬度：' + res.latitude);
				this.latitude     =  res.latitude;
				this.longitude    =  res.longitude;
				this.latitudeDef  =  res.latitude;
				this.longitudeDef =  res.longitude;
				this.center    = [res.latitude, res.longitude];
			},
			fail:function(){
				uni.hideLoading();
			}
		});
	},
	methods:{
		regionchange : function(e){
			var map = uni.createMapContext('map', this);
			map.getCenterLocation({
				success:(res)=>{
					console.log(res);
					this.center    = [res.latitude, res.longitude];
				}
			})
		}
	},
}
</script>
